import * as Common from './action-type'
import config from '@/config'

let defaultState = {
  currentTheme: 'default',//
  collapsed: config.collapsedDefaultOpen || true,//
  breadcrumbData: [],//
  reload: false,
  userInfo: null,//
  windowInfo: {
    screenHeight: window.innerHeight || document.body.clientHeight || document.documentElement.clientHeight,//
    screenWidth: window.innerWidth || document.body.clientWidth || document.documentElement.clientWidth,//
    mainContentHeight: '',//
    mainContentWidth: '',//
  }
};

let CommonReducer = (state = defaultState, action) => {
  switch (action.type) {
    case Common.COLLAPSEDTOGGLE:
      return {...state, ...{collapsed: action.value}};
    case Common.SETBREADCRUMB:
      return {...state, ...{breadcrumbData: action.value}};
    case Common.RELOAD:
      return {...state, ...{reload: action.value}};
    case Common.WINDOWINFO:
      return {...state, ...{windowInfo: action.value}};
    case Common.USERINFO:
      return {...state, ...{userInfo: action.value}};
    case Common.CURRENTTHEME:
      return {...state, ...{currentTheme: action.value}};
    default:
      return state;
  }
};

export default CommonReducer;
